<template>
  <v-card
    :loading="loading === 1"
    flat
    class="px-3"
  >
    <v-card-title>{{ item.text }}</v-card-title>
    <v-card-text class="text-h3 white--text font-weight-bold">
      <span>{{ data ? data[property][item.value] : '?' }}</span>
      <span class="text-subtitle-1 font-weight-bold ml-1">{{ icon }}</span>
    </v-card-text>
    <slot />
  </v-card>
</template>

<script>
export default {
  props: {
    loading: {
      type: Number,
      required: true,
    },
    item: {
      type: Object,
      required: true,
    },
    data: {
      type: Object,
      default() {
        return {};
      },
    },
    property: {
      type: String,
      required: true,
    },
    icon: {
      type: String,
      required: true,
    },
  },
};
</script>
